.leitourgia {
  .component-header {
      color: #36648e;
      margin: 5% 0;
      padding: 3%;
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 24px;
      font-weight: bold;
      text-align: center;
      border: 2px solid #36648e;
      background: #fff;
  }
}
.leitourgia-wrapper {
  @include layout($symmetric);
  @include container;
  margin-bottom: 2rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .leitourgia-group {
      .type {
        display: flex;
        height: 100%;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        font-weight: bold;
        padding: 1rem;
      }
  }
}

 $images: bag, younggirl, vivliothiki, diary, books, smallbooks,younggirl, vivliothiki, diary;
   @for $iv2 from 1 through length($images) {
    .frontpagev3 .spoudes-group:nth-child(#{$iv2}n) {
        background: url(/assets/images/bg-images/#{nth($images, $iv2)}.jpg);
        }
      }
   @for $iv2 from 1 through length($images) {
    .leitourgia-group:nth-child(#{$iv2}n) {
        background: url(/assets/images/bg-images/#{nth($images, $iv2)}.jpg);
        background-size: cover;
        }
      }
  .leitourgia-group {
      @include layout($symmetric);
      margin-bottom: 15px;
      display: flex;
    }
  .leitourgia-group .type {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1em;
    background-color: rgba(18, 23, 32, 0.7);    
  }
  .leitourgia-group .type:hover {
    background-color: rgba(18, 23, 32, 0.4);

  }
  .leitourgia-group .type a {
    color: #fff;
    font-weight: bold;
    border-width: 1px;
    border-style: solid;
    border-color: rgba(18, 23, 32, 0);
    text-align: center;
    font-size: 28px;
    line-height: 1.3;
    letter-spacing: 2px;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .leitourgia-group .type a:hover {
    border: 1px solid #fff;
    transition: border-color 1s ease;
    background: rgba(0, 0, 0, 0.2);
    font-size: 30px;
    transition: font 1s ease;
  }


@include susy-media(normal) {
  .leitourgia-group {
    margin-bottom: gutters(12);
    height: 250px;
    flex-basis: span( 6 of 12);
    }
  .leitourgia-group:nth-child(3) .type a{
      padding: 2em;
    } 
  .leitourgia-group:nth-child(5) .type a{
      padding: 1.5em;
    }
  .leitourgia-group .type a {
    font-size: 28px;
  }
  .leitourgia-group .type a:hover {
    font-size: 30px;
  }
}

@include susy-media(mobile) {
  .leitourgia-group {
    margin-bottom: gutters(12);
    height: 150px;
    flex-basis: 100%;
    }
  .leitourgia-group .type a {
    font-size: 22px;
    }
  .leitourgia-group .type a:hover {
    font-size: 24px;
    }
}